.container {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  width: calcWidth($card-width, $card-margin-right);
  margin: $card-magrins-vert;

  &__card {
    margin-right: $card-margin-right;
    margin-bottom: $card-margin-bottom;

    &:nth-child(4n) {
      margin-right: 0;
    }
  }
}
